}
.Font {
    width: 200px;
    background-color: #fff;
    padding: 10px;
    border: 1px solid rgba(0,0,0,.1);
    margin-bottom: 10px;
    box-shadow: 0 2px 4px rgba(0,0,0, .08);
}

.Font__header {
    padding-bottom: 5px;
    border-bottom: 1px solid rgba(0,0,0,.1);
    margin-bottom: 5px;
}

.Font__header p {
    margin-bottom: 0;
}

.Font__label {
    margin: 0 0 5px 0;
}

.Font__label select {
    font-size: 12px;
    margin-left: 3px;
}

.Font__label input[type="color"] {
    margin-left: 3px;
}
.Typing {
    width: 200px;
    background-color: #fff;
    padding: 10px;
    border: 1px solid rgba(0,0,0,.1);
    margin-bottom: 10px;
    box-shadow: 0 2px 4px rgba(0,0,0, .08);
}

.Typing__header {
    padding-bottom: 5px;
    border-bottom: 1px solid rgba(0,0,0,.1);
    margin-bottom: 5px;
}

.Typing__header p {
    margin-bottom: 0;
}

textarea {
    font-size: 15px;
    height: 80px;
    width: 180px; 
    resize: none;
}

.Typing__clearBtn {
    width: 100%;
    padding: 0 20px;
    display: block;
    margin: 0 auto;
}

.Typing__clearBtn:focus {
    box-shadow: none;
}
.SideBarFooter {
    width: 200px;
    background-color: #fff;
    padding: 10px;
    border: 1px solid rgba(0,0,0,.1);
    box-shadow: 0 2px 4px rgba(0,0,0, .08);
}

.btn-primary:focus, .btn-outline-primary:focus {
    box-shadow: none;
}
.ListItem {
    position: relative;
    display: flex;
    height: 100px;
    border-bottom: 1px solid rgba(0,0,0,.08);
    border-left: none;
    cursor: pointer;
}

.ListItem__active {
    border-left: 5px solid #007bff;
    transition: all .2s linear;
}

.ListItem__left {
    width: 25%;
    background-color: #f8f8f8;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}


.ListItem__right {
    width: 75%;
    height: 100.5%;
    position: relative;
}

/**********************************************/
/*DATE*/

.ListItem__date {
    display: flex;
    flex-direction: row;
    height: 50px;
    flex-grow: 1;
    flex-shrink: 0;
    align-items: center;
    -webkit-transform: translateX(-2px);
            transform: translateX(-2px);
}

.ListItem__date--month {
    font-family: "Ariel", sans-serif;
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
    border-bottom: 1px solid #999;
    padding: 0;
    font-weight: 700;
    font-size: 16px;
    text-transform: uppercase;
}

.ListItem__date--day {
    font-family: "Ariel", sans-serif;
    font-weight: 700;
    font-size: 40px;
    -webkit-transform: translateX(-3px);
            transform: translateX(-3px);
}

.ListItem__date--year {
    font-family: "Ariel", sans-serif;
    font-size: 18px;
    color: #999;
    font-weight: 600;
    letter-spacing: 8px;
    -webkit-transform: translate(4px, -3px);
            transform: translate(4px, -3px);
}

/**********************************************/
/*COLOR INFO*/

.ListItem__colorInfo {
    background-color: rgba(255,255,255,.6);
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    color: #333;
    padding: 0 5px;
    box-shadow: 0 0 2px rgba(0,0,0,.14);
    transition: all .2s linear;
}

.ListItem:hover .ListItem__colorInfo {
    opacity: 1;
}

/**********************************************/
/*BUTTON*/
.ListItem__deleteBtn {
    cursor: pointer;
    position: absolute;
    bottom: 5px;
    right: 5px;
    padding: 3px;
    font-size: 10px;
    opacity: 0;
    transition: all .2s linear;
}

.ListItem:hover .ListItem__deleteBtn {
    opacity: 1;
}
.ListView {
    padding: 10px;
}

.ListView__top {
    height: 200px;
    overflow: auto;
    margin-bottom: 10px;
    text-align: center;
    padding-top: 20px;
}

.ListView__info--title {
    font-weight: 600;
    color: #333;
    text-transform: uppercase;
    font-size: 15px;
    padding-bottom: 3px;
    margin-bottom: 5px;
    border-bottom: 1px solid rgba(0,0,0,.1);
}

.ListView__info--content {
    font-size: 13px;
    margin-bottom: 2px;
    color: #333;
}
.ListModel {
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    background-color: rgba(0,0,0,.6);
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 0;
    transition: all .3s linear;
}

.active {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
    transition: all .3s linear;
}

.ListModel__content {
    display: flex;
    position: fixed;
    width: 800px;
    height: 550px;
    background-color: #f8f8f8;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    box-shadow: 0 2px 4px rgba(0,0,0,.24);
}

.ListModel__content--left {
    width: 350px;
    overflow: scroll;
}

.ListModel__content--right {
    width: 450px;
    height: 400px;
    padding: 10px;
}

.ListModel__empty {
    width: 300px;
    margin: 30% auto;
    text-align: center;
}

.ListModel__empty img {
    width: 150px;
    margin-bottom: 10px;
}

.ListModel__empty--author {
    font-size: 8px;
    margin-top: 180px;
    color: #999;
}

/*******************************************/
/*BUTTON*/
.ListModel__btn {
    position: fixed;
    padding-bottom: 20px;
    top: -25px;
    left: 788px;
    font-weight: 700;
    background-color: transparent;
    border: none;
    color: #fff;
    cursor: pointer;
}

.ListModel__btn:hover {
    color: #333;
}

.deleteAllBtn {
    display: block;
    margin-left: auto;
    margin-right: 5px;
    font-size: 12px;
    font-weight: 500;
    color: #f8f8f8;
    box-shadow: 0 2px 4px rgba(0,0,0,.12);
}
